.modal {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1000;
    border: none;
}

.modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #3C3C56;
    padding: 20px;
    color: #FFFFFF;
    border: none;
    border-radius: 20px;
}

.close {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    font-size: 24px;
}
.close img {
    width: 40px;
}
@media (max-width: 768px) {
    .modal-content {
        width: 80%; /* Уменьшим ширину для мобильных экранов */
        padding: 10px; /* Уменьшим отступы */
    }

    .close {
        font-size: 20px; /* Уменьшим размер кнопки закрытия */
        top: 5px;
        right: 5px;
    }
}