.Info {
    display: flex;
    justify-content: space-around;

}
.Info-button {
    display: flex;
    justify-content: space-between;
}
.Info-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 50%; /* Each block occupies 50% */
    padding: 0 250px; /* Add padding for spacing */

}

.Info-right {
    flex: 1;
    display: flex;
    align-items: center;
    width: 50%; /* Each block occupies 50% */
    padding: 0 20px; /* Add padding for spacing */
}

.Info-text {
    color: white;
    font-size: 45px;
    font-weight: 700;

}
.registration {
    margin-top: 25px ;
}

.come {
    width: 125px;
    height: 30px;
    top: 208px;
    left: 20px;
    padding: 5px;
    border-radius: 41px;
    gap: 10px;
    color: #00B9F8;
    cursor: pointer;
    border: none;
    margin-right: 25px;
}

.Become-a-member {
    width: 200px;
    height: 30px;
    top: 208px;
    left: 132px;
    padding: 5px;
    border-radius: 41px;
    gap: 10px;
    cursor: pointer;
    border: none;
    background: linear-gradient(90.09deg, #7359EA -1.33%, #AE61FF 41.19%, #7359EA 99.93%);
    color: white;
}

.WelcomePage {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.Info {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Info-button {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    background-color: #3C3C56;
    color: white;
}

.Untitled img {
    width: 150%;
}
.Company-name {
    color: gray;
}
@media (max-width: 768px) {
    .Info {
        flex-direction: column; /* Отображать блоки вертикально */
        align-items: center;
        margin: 20px;
    }
    .Info-left {
        width: 100%; /* Один блок занимает всю ширину */
        padding: 0 20px;
        display: block;
    }
    .Info-right {
        width: 100%; /* Один блок занимает всю ширину */
        padding: 0 20px;
    }

    .Info-button {
        flex-direction: column;
        padding: 10px;
    }

    .come{
        width: 60%;
        margin: 10px;
    }
    .Become-a-member {
        width: 100%;
        margin: 10px;
    }

    .Untitled img {
        width: 100%; /* Занимает всю доступную ширину */
    }
    .Info-text {
        color: white;
        font-size: 20px;
        font-weight: 700;

    }
    .registration {
       display: flex;
    }
}
