.formlogin {
    display: flex;
    flex-direction: column;

    justify-content: center;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
    margin-right: 140px;
    margin-left: 140px;

}


.login, .password {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: 24px; /* Размер изображения */
    background-repeat: no-repeat;
    background-position: 10px center; /* Расположение изображения внутри элемента */
    margin-top: 15px;
    margin-bottom: 15px;
}

input[type="text"], input[type="password"] {
    border: none;
    border-radius: 20px;
    padding: 13px;
    width: 340px;
    background: #E8E6FF;
    text-align: start;
}

.helppassword {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    font-size: 10px;
    width: 100%;
}

.Show {
    margin-right: 20px;
    cursor: pointer;
    color: white;
    display: flex;
    align-items: center;
}

.forget {
    text-decoration: none;
    cursor: pointer;
    color: white;
    display: flex;
    align-items: center;
}
:hover.forget {
    color: #4d4d4d;
}
.buttonsubmit{
    margin-top: 20px;
}
.buttonsubmit button {
    border: none;
    border-radius: 20px;
    padding: 12px 50px;
    background: linear-gradient(90deg, #7B6CEA 9.12%, #AE61FF 42.93%, #7359EA 94.89%);
    color: white;
    cursor: pointer;
    font-size: 15px;
}

.buttonsubmit button:hover {
    background-color: #888888;
}
.label-for-showPassword {
    display: inline-block;
    width: 30px; /* Задайте желаемую ширину и высоту для создания круглой формы */
    height: 30px;
    border-radius: 50%; /* Делаем круглой */
    background: linear-gradient(90deg, #7B6CEA 9.12%, #AE61FF 42.93%, #7359EA 94.89%);
    color: white; /* Цвет текста */
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

input[type="checkbox"] {
    display: none;
    touch-action: manipulation;
}

input[type="checkbox"] + label {
    position: relative;
    padding-left: 20px; /* Размер круглой области */
    cursor: pointer;
    padding-top: 2px;
    touch-action: manipulation;
}


input[type="checkbox"] + label::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 15px; /* Диаметр круга */
    height: 15px; /* Диаметр круга */
    border-radius: 50%; /* Превращаем в круг */
    background: linear-gradient(90deg, #7B6CEA 9.12%, #AE61FF 42.93%, #7359EA 94.89%);
    touch-action: manipulation;
}

/* Отображаем галочку при отмеченном чекбоксе */
input[type="checkbox"]:checked + label::before {
    content: "\2713"; /* Юникод-символ галочки */
    text-align: center;
    font-size: 14px; /* Размер символа */
    line-height: 15px; /* Выравнивание символа по вертикали */
    color: #3D405F; /* Цвет символа */
    touch-action: manipulation;
}


.login input {
    background-image: url("../images/login.svg"); /* Путь к изображению для логина */
    background-size: 20px; /* Размер изображения */
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 40px; /* Добавьте отступ слева, чтобы изображение не накладывалось на текст */
}


.password input[type="password"] {
    background-image: url("../images/password.svg");
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 40px;
    touch-action: manipulation;
}
.password input[type="text"] {
    background-image: url("../images/password.svg");
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 40px;
    touch-action: manipulation;
}
@media (max-width: 768px) {
    .formlogin {
        margin: 15px;
    }

    .login, .password {
        margin: 10px;
    }

    input[type="text"], input[type="password"] {
        width: 100%;
        touch-action: manipulation;
    }

    .helppassword {
        gap: 10px;
    }

    .label-for-showPassword {
        display: none;
    }
}
