.Header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    padding: 15px;
}
.logo img {
    width: 150px;
    margin-left: 60px;
}
.logoimg {
    margin-left: 40px;
}

.navigcia-first {
    list-style: none;
    cursor: pointer;
}
.navigcia-first:hover {
    color: gray;
}
.navigcia-centr {
    list-style: none;
    cursor: pointer;
}
.navigcia-centr:hover {
    color:gray;
}
.navigcia-last {
    list-style: none;
    cursor: pointer;
}
.navigcia-last:hover {
    color: gray;
}

.navigcia li {
    display: inline-block;
    margin-right: 20px;
    font-size: 19px;


}
.nav-and-lang {
    display: flex;
    align-items: center;
    gap: 20px;

}
.langselect {
    margin-right: 30px;
    height: 28px;
    width: 120px;
    font-size: 15px;
    border-radius: 5px; /* Normal border radius */
    background-color: #00B9F8;
    color: white;
}



.langselect option {
    background-color: white;
    color: black;
    border-radius: 0px;
}
.mobile-menu {
    display: none; /* По умолчанию скрыть мобильное меню */

}
.mobile-menu-button {
    display: none; /* По умолчанию скрыть кнопку на больших экранах */

}
.togl-mob {
    display: none;
}


@media (max-width: 768px) {
    .Header {
        padding: 10px; /* Меньший отступ */
    }
    .nav-and-lang {
        display: none;
        align-items: center;
        gap: 20px;

    }
    .togl-mob {
        display: block;
        margin-right: 50px;
        margin-top: 10px;
    }

    .langselect-mob {
        height: 28px;
        width: 120px;
        font-size: 15px;
        border-radius: 5px; /* Normal border radius */
        background-color: #00B9F8;
        color: white;
    }
    .langselect-mob option {
        background-color: white;
        color: black;
        border-radius: 0px;
    }
    .logo img {
        width: 100px; /* Уменьшаем ширину логотипа */
        margin-left: 10px; /* Уменьшаем отступ слева */
    }

    .logoimg {
        margin-left: 10px; /* Уменьшаем отступ слева */
    }

    .navigcia li {
        display: block; /* Отображаем элементы списка в столбец */
        margin-right: 0; /* Убираем правый отступ */
    }

    .langselect {
        margin-right: 10px; /* Уменьшаем отступ справа */
        width: 100px; /* Уменьшаем ширину селекта */
        font-size: 14px; /* Уменьшаем размер шрифта */
    }
    /* Стили для кнопки гамбургера */

    .mobile-menu-button {
        display: block;
        cursor: pointer;
        font-size: 24px;
        position: absolute;
        top: 15px;
        right: 15px; /* Разместить кнопку в правом верхнем углу */
    }

    .navigcia-first-mob {
        list-style: none;
        cursor: pointer;
    }
    .navigcia-first-mob:hover {
        color: gray;
    }
    .navigcia-centr-mob {
        list-style: none;
        cursor: pointer;
    }
    .navigcia-centr-mob:hover {
        color:gray;
    }
    .navigcia-last-mob {
        list-style: none;
        cursor: pointer;
    }
    .navigcia-last-mob:hover {
        color: gray;
    }
    /* Стили для мобильного меню */
    .mobile-menu {
    display: block;
        background-color: #3C3C56; /* Цвет фона */
        position: absolute;
        top: 63px; /* Расположить меню под шапкой */
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
        right: 0;
    }

    /* Стили для элементов в мобильном меню */
    .mobile-menu a {
        display: block;
        color: white;
        text-decoration: none;
        margin-bottom: 10px;
        font-size: 18px;
    }
.nav-and-lang-mob {
    display: flex;
    flex-direction: column; /* Разместить элементы в столбце */
    align-items: flex-end; /* Выравнивать элементы справа */
}

    /* Стили для блока меню */
    .menu-block {
        display: flex;
        flex-direction: column; /* Размещение элементов в столбце */
        align-items: flex-end; /* Выравнивание элементов справа */
        padding: 10px; /* Внутренний отступ блока */
        background-color: #3C3C56; /* Цвет фона блока */
    }
}
