.inforeg {
    display: flex;
    justify-content: space-between;
    display: flex;
    margin-top: 15px;
    margin-bottom: 15px;
    margin-right: 120px;
    margin-left: 120px
}
.agreements {
    font-size: 9px;
    width: 75%;
    align-items: start;
}
.forgetreg {
text-decoration: none;
cursor: pointer;
color: white;
display: flex;
align-items: center;
}
.helppasswordreg {
display: flex;
justify-content: space-between;
align-items: center;
margin: 10px;
font-size: 12px;
    width: 100%;
}

.loginreg {
    display: flex;
    margin-top: 15px;
    margin-bottom: 15px;
}
.loginregagreements {
    display: flex; /* Добавьте стиль flex для размещения чекбокса и текста в одной строке */
    width: 100%;
}

.loginregagreements input[type="checkbox"] {
    margin-right: 10px; /* Добавьте небольшой отступ между чекбоксом и текстом */
}

.loginregagreements .agreements {
    flex: 1; /* Занимает оставшееся пространство, чтобы текст занимал всю доступную ширину */
    font-size: 15px;
}
.loginreg input[type="text"] {
    border: none;
    border-radius: 20px;
    padding: 15px;
    width: 100%;
    background: #E8E6FF;
    text-align: start;
}
.loginreg input[type="password"] {
    border: none;
    border-radius: 20px;
    padding: 15px;
    width: 100%;
    background: #E8E6FF;
    text-align: start;
}
.formloginreg {
    display: flex;
    flex-direction: column;

    justify-content: center;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
    margin-right: 200px;
    margin-left: 200px;

}
.buttonsubmitreg button {
    border: none;
    border-radius: 20px;
    padding: 12px 50px;
    background: linear-gradient(90deg, #7B6CEA 9.12%, #AE61FF 42.93%, #7359EA 94.89%);
    color: white;
    cursor: pointer;
    font-size: 15px;
}

.buttonsubmitreg button:hover {
    background-color: #888888;
}

@media (max-width: 768px) {
    .loginreg, .loginregagreements {
        margin: 10px; /* Уменьшаем отступы для мобильных устройств */
    }

    .agreements {

        font-size: 14px; /* Уменьшаем размер шрифта */
    }

    .helppasswordreg {
        gap: 10px; /* Уменьшаем горизонтальный отступ между элементами */
        font-size: 8px; /* Уменьшаем размер шрифта */
    }

    .forgetreg {
        font-size: 8px; /* Уменьшаем размер шрифта */
    }

    .loginreg input[type="text"],
    .loginreg input[type="password"] {
        text-align: left; /* Левое выравнивание текста */
        font-size: 14px; /* Уменьшаем размер шрифта */
        padding: 10px; /* Уменьшаем внутренний отступ */
    }
    .formloginreg {
        justify-content: start;
        text-align: start;
        margin-top: 40px;
        margin-bottom: 40px;
        margin-right: 20px;
        margin-left: 20px;
    }
    .loginregagreements .agreements {
        flex: 1 1;
        font-size: 10px;
    }
    .buttonsubmitreg button {
        margin-left: 60px;
    }

}


